<template>
  <div>
    <base-header class="pb-6 pb-8 pt-5 pt-md-8 ">
      <!-- Card stats -->
    </base-header>
    <div class="mb-3 mt-3 col-sm-12 col-md-6 col-lg-12">
      <button class="btn btn-default" @click="exportTableToExcel">Export to Excel</button>
    </div>
    <div class="table-responsive">

      <div class=" mt-5 p-3">
        <h2>11-sinf Jami:- {{all_users.total}}</h2>
        <table class="table table-bordered table-hover" id="my-table">
          <thead class="thead-light">
          <tr>
            <th rowspan="2">T/R</th>
            <th colspan="2">F.I.O</th>
            <th colspan="2">Sinf</th>
            <th colspan="9">Savollarga javoblar*</th>
          </tr>
          <tr>
            <td> </td>
            <td > </td>
            <td> </td>
            <td> </td>
            <th>1-savol</th>
            <th>2-savol</th>
            <th>3-savol</th>
            <th>4-savol</th>
            <th>5-savol</th>
            <th>6-savol</th>
            <th>7-savol</th>
            <th>8-savol</th>
            <th>9-savol</th>
          </tr>
          </thead>
          <tbody>
          <!-- Example row -->
          <tr v-for="(item,index) in all_users.results" :key="index">

            <td> {{index+1}}</td>
            <td colspan="2">{{item.full_name}} </td>
            <td colspan="2">{{item.class_name}} </td>
            <td v-if="item.questionnaire_answers[0]&&item.questionnaire_answers[0].choices[0]">{{item.questionnaire_answers[0].choices[0].text}} </td>
            <td v-if="item.questionnaire_answers[1]">{{item.questionnaire_answers[1].choices}}</td>
            <td v-if="item.questionnaire_answers[2]">{{item.questionnaire_answers[2].choices}}</td>
            <td v-if="item.questionnaire_answers[3]">{{item.questionnaire_answers[3].choices}}</td>
            <td></td>
            <td></td>
            <td></td>
            <td></td>
            <td></td>
            <td></td>
          </tr>

          </tbody>
        </table>

        <iframe id="dummyFrame" style="display:none"></iframe>
      </div>

    </div>
  </div>
</template>
<script>
import axios from "axios";
import * as XLSX from 'xlsx';
import { saveAs } from 'file-saver';
export default {
  data(){
    return{
      all_users:[],
      statistiks:[],
      statsExel: [],
      regions_main:[],
      sum:[],
      all_sum:0,
      gender_m:[],
      gender_w:[],
      gender_all_m:0,
      gender_all_w:0,
    }
  },
  mounted() {
    this.sum.push(this.$route.params.id)
    axios.get(`https://api-kasb.maktab.uz/api/v1/admin/questionnaire-answers/?school_id=${this.$route.params.id}&&class_id=11`, {
      headers: {
        'Authorization': `Bearer ${localStorage.getItem('token')}`,
        'Accept-Language': 'uz'
      }
    })
      .then(res=>{
        this.all_users=res.data
      })



  },
  methods:{
    exportTableToExcel() {
      const table = document.getElementById('my-table');
      const worksheet = XLSX.utils.table_to_sheet(table);
      const workbook = XLSX.utils.book_new();
      XLSX.utils.book_append_sheet(workbook, worksheet, 'Sheet1');

      const wbout = XLSX.write(workbook, { bookType: 'xlsx', type: 'array' });

      try {
        saveAs(new Blob([wbout], { type: 'application/octet-stream' }), 'table_data.xlsx');
      } catch (e) {
        if (typeof console !== 'undefined') console.log(e, wbout);
      }
    }
  }
}
</script>
